



























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {
    HelloWorld
  },
  filters: {
    getFormatDate (date: any) {
      return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate()
    },
    fenToYuan (num: any) {
      if (typeof num !== 'number' || isNaN(num)) return null
      return '¥' + (num / 100).toFixed(2)
    }
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  // 声明周期钩子
  model = { phone: '', openid: '' }
  fields =[{ type: 'input', modelKey: 'phone', label: '手机号', props: { placeholder: '请输入' }, rules: { required: true, type: 'tel' } }, { type: 'input', modelKey: 'verificationCode', label: '验证码', props: { placeholder: '请输入', type: 'text' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  options = {}
  time = 60 // 验证码等待时间
  sendMsgDisabled = false
  isVerification = false // 是否需要开启验证码
  openid = ''
  jump = '' // 登陆成功跳转页

  actions = [
    {
      text: '合计 ¥00.00',
      action: 'moreMoney'
    },
    {
      type: 'button',
      text: '<div class="payButton">立即缴费</div>',
      action: 'payButtonClick'
    }
  ]
  selectCost = 0 // 选择总费用 单位 分
  totalCost = 0 // 总费用 单位 分
  checkList: any = []
  billList = []
  houseId = ''
  // tab切换配置
  selectedLabelDefault = 'incomplete'
  tabBarList = [{
    label: '未缴账单',
    value: 'incomplete'
  }, {
    label: '已缴账单',
    value: 'complete'
  }]

  // 已缴费用
  page = 1
  isShowMore = true
  mounted () {
    this.getWeiXinConfig()
    this.pageInit()
  }

  activated () {
    this.getWeiXinConfig()
    this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log(result)
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getBillListByHouseId()
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }
  pageInit () {
    let query = this.$route.query
    if (query.houseId) {
      this.houseId = query.houseId
    } else {
      this['$createDialog']({
        type: 'alert',
        content: '登陆方式错误',
        icon: 'cubeic-wrong'
      }).show()
      return
    }
    if (this.$store.state.sessionId) {
      this.getBillListByHouseId()
    } else if (query.code) {
      this.getBindingInfo()
    }
  }
  getBillListByHouseId () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getBillListByHouseId', { houseId: this.houseId, type: this.selectedLabelDefault, page: this.page, limit: 1 }).then((response) => {
      if (response.success) {
        if (this.selectedLabelDefault === 'incomplete') {
          this.billList = response.data.list
          this.initCheckbox()
        } else {
          if (response.data.list.length > 0) {
            this.billList = this.billList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
        }
      }
      toast.hide()
    })
  }
  initCheckbox () {
    let arr:any = []
    let totalCost = 0
    this.billList.forEach((item:any) => {
      arr.push(item.id)
      totalCost += item.cost
    })
    this.totalCost = totalCost
    this.selectCost = totalCost
    this.actions[0].text = this.fenToYuan(totalCost)
    this.checkList = arr
  }
  computedPrice (list: any) {
    let totalCost = 0
    this.billList.forEach((item) => {
      list.forEach((id:any) => {
        if (item.id === id) {
          totalCost += item.cost
        }
      })
    })
    this.selectCost = totalCost
    this.actions[0].text = this.fenToYuan(totalCost)
  }
  checkboxChange (list: any) {
    this.computedPrice(list)
  }
  clickHandler (item: any) {
    if (item.action) {
      this[item.action](item)
    }
  }
  fenToYuan (num: any) {
    if (typeof num !== 'number' || isNaN(num)) return null
    return '合计 ¥' + (num / 100).toFixed(2)
  }
  getWeiXinConfig () {
    // let url = window.location.origin + window.location.pathname
    let url = window.location.href
    this.$store.dispatch('getWeixinConfig', { url: url }).then((res)=>{
      console.log('获取微信配置成功', res)
      if (res.success) {
        let config = res.data
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: config.appId, // 必填，公众号的唯一标识
          timestamp: config.timestamp, // 必填，生成签名的时间戳
          nonceStr: config.nonceStr, // 必填，生成签名的随机串
          signature: config.signature, // 必填，签名
          jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表
        })
      }
      wx.ready(function() {
        console.log('ready========')
      })
      wx.error(function(err:any) {
        console.log(err)
        console.log('errrrrrrrrrrrrrr')
      })
    })
  }

  payButtonClick () {
    let that = this
    let query = {}
    query.rqBillList = this.checkList
    query.fee = this.selectCost
    query.body = '物业缴费'
    this.$store.dispatch('setUppropertyOrder', query).then((response) => {
      if (response.success) {
        let payData = response.data
        wx.chooseWXPay({
          timestamp: payData.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
          nonceStr: payData.nonceStr, // 支付签名随机串，不长于 32 位
          package: payData.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
          signType: payData.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
          paySign: payData.paySign, // 支付签名
          success: function (res) {
            // 支付成功后的回调函数
            const toast = that.$createToast({
              time: 2000,
              txt: '订单处理中...',
              mask: true,
              onTimeout: () => {
                that.getBillListByHouseId()
                that['$createDialog']({
                  type: 'alert',
                  content: '缴费成功',
                  icon: 'cubeic-ok'
                }).show()
              }
            })
            toast.show()
          },
          error: function () {

          }
        })
      }
    })
  }

  tabBarChange (value:any) {
    this.page = 1
    this.isShowMore = true
    this.billList = []
    this.getBillListByHouseId()
  }
  showMoreClick () {
    this.page++
    this.getBillListByHouseId()
  }
}
