
































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import wx from 'weixin-js-sdk'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class Home extends Vue {
  hello : string = 'Hello World!'
  msg = 123
  $createDialog : any
  // 声明周期钩子
  model = { inputValue: '', pcaValue: [], dateValue: '' }
  fields =[{ type: 'input', modelKey: 'inputValue', label: 'Input', props: { placeholder: '请输入' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  // 小区选择数据
  houseId = ''
  listData = []
  accountInfo = {}
  showSelect = false
  title = '房屋选择'
  placeholder = '请选择房屋'
  mounted () {
    this.pageInit()
  }

  activated () {
    this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  @Watch('$route')
  onRouterChanged () {
    // this.getWeiXinConfig()
  }

  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  submitHandler (e : any) {
    console.log('submit')
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type })
  }
  scanQRCode () {
    let that = this
    wx.scanQRCode({
      needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
      scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
      success: function (res : any) {
        var result = res.resultStr // 当needResult 为 1 时，扫码返回的结果
        that.$store.dispatch('qrValidate', {qrId: result}).then((res)=>{
          if (res && res.success) {
            that.$createDialog({
              type: 'alert',
              title: '核销成功',
              content: '核销成功',
              icon: 'cubeic-right'
            }).show()
          } else {
             that.$createDialog({
              type: 'alert',
              title: '核销失败',
              content: '此码无效！',
              icon: 'cubeic-wrong'
            }).show()
          }
        })
      }
    })
  }
  getWeiXinConfig () {
    this.$store.dispatch('getWeixinConfig', { user: '13934691550', pass: 'wangtao' }).then((res)=>{
      console.log('获取微信配置成功', res)
      if (res.success) {
        let config = res.data
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: 'wxc6c897a704d3fac4', // 必填，公众号的唯一标识
          timestamp: config.timestamp, // 必填，生成签名的时间戳
          nonceStr: config.nonceStr, // 必填，生成签名的随机串
          signature: config.signature,// 必填，签名
          jsApiList: ['scanQRCode'] // 必填，需要使用的JS接口列表
        });
      }
      wx.ready(function() {
        console.log('ready========')
      })
      wx.error(function(err:any) {
        console.log(err)
        console.log('errrrrrrrrrrrrrr')
      })
    })
  }

  getHouseList () {
    this.$store.dispatch('getHouseList', {}).then((response) => {
      if (response.data.list) {
        this.listData = response.data.list
        this.houseId = this.listData[0].id
      }
    })
  }

  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getHouseList()
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }

  pageInit () {
    let query = this.$route.query
    if (this.$store.state.sessionId) {
      this.getHouseList()
    } else if (query.code) {
      this.getBindingInfo()
    }
  }

  toOtherPage (type: any) {
    if (type === 'pay') {
      this.$router.push({ path: '/pay', query: { houseId: this.houseId} })
    }
  }

  get options () {
    this.listData.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' '+ obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室'
    })
    return this.listData
  }
}
